/* Product Card */

section#featuredProducts div.productCard {
	display: flex;
	width: 30rem;
	flex-direction: column;
	align-items: flex-start;
	flex-shrink: 0;

	border-radius: 2rem;
	border: 1px solid rgba(255, 255, 255, 0.50);
	border-right-color: #EAE4F0;
/*  border-bottom-color: #f2e9fa;*/
  border-bottom-color: #eae4f0;
  box-sizing: border-box;
	background: linear-gradient(105deg, rgba(255, 255, 255, 0.40) 16.65%, rgba(255, 255, 255, 0.10) 79.09%);
	/* box-shadow: 1px 0px 0px 0px rgba(255, 255, 255, 0.30) inset, 0px 15px 30px 0px rgba(23, 14, 65, 0.03); */
		/* box-shadow:
		1px 0px 0px 0px rgba(255, 255, 255, 0.30) inset,
	  0.8px 0.8px 2.2px rgba(23, 14, 65, 0.003),
	  2px 2px 5.3px rgba(23, 14, 65, 0.004),
	  3.8px 3.8px 10px rgba(23, 14, 65, 0.005),
	  6.7px 6.7px 17.9px rgba(23, 14, 65, 0.006),
	  12.5px 12.5px 33.4px rgba(23, 14, 65, 0.007),
	  30px 30px 80px rgba(23, 14, 65, 0.01); */

		box-shadow:
		1px 0px 0px 0px rgba(255, 255, 255, 0.30) inset,
		0.8px 0.8px 2.2px rgba(23, 14, 65, 0.006),
		2px 2px 5.3px rgba(23, 14, 65, 0.008),
		3.8px 3.8px 10px rgba(23, 14, 65, 0.01),
		6.7px 6.7px 17.9px rgba(23, 14, 65, 0.012),
		12.5px 12.5px 33.4px rgba(23, 14, 65, 0.014),
		30px 30px 80px rgba(23, 14, 65, 0.02);
}

div.productCard header {
	padding: 2rem;
	display: flex;
	flex-direction: column;
	align-self: stretch;
}

div.productCard header .photoBlock {
	position: relative;
	display: flex;
	height: 25.2rem;
	justify-content: center;
	align-items: center;
	align-self: stretch;
	border-radius: 2rem;
	background: var(--white-100, #FFF);
	box-shadow: 0px 8px 16px 0px rgba(23, 14, 65, 0.02), 1px 1px 1px 0px rgba(255, 255, 255, 0.50) inset;
}

/* Product Brand Tag  */
div.productCard header .photoBlock::after {
	content: attr(brand);
	position: absolute;
	right: 0rem;
	top: 2.8rem;

	display: flex;
	padding: 0.5rem 1.3rem;
	justify-content: center;
	align-items: center;

	border-radius: 1rem 0rem 0rem 1rem;
	border: 1px dashed #E4C8FC;
	border-right-width: 0px;
	background: #FCF8FF;

	color: var(--primary-1, #10002B);
	font-size: var(--fs-10);
	font-weight: var(--fw-regular);
	text-transform: uppercase;
	line-height: 133.2%; /* 1.332rem */
	letter-spacing: 0.1rem;
}

div.productCard header img {
	width: 15.4rem;
	height: 20rem;
	flex-shrink: 0;
}

div.productCard footer {
	display: flex;
	height: 15.2rem;
	padding: 0.4rem 2.6rem 2rem 2.6rem;
	flex-direction: column;
	justify-content: flex-end;
	align-items: center;
	gap: 2.4rem;
	align-self: stretch;
}

div.productCard footer .detailsBlock {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: flex-start;
	gap: 1.4rem;
	align-self: stretch;
}

div.productCard footer .detailsBlock h5 {
	color: var(--primary-1, #10002B);
	font-family: var(--ff-body);
	font-size: var(--fs-13);
	font-weight: var(--fw-bold);
	line-height: 133.2%; /* 1.7316rem */
	letter-spacing: 0.14rem;
	text-align: left;
	text-transform: uppercase;
}

div.productCard footer .priceBlock{
	display: flex;
	align-items: center;
	gap: 1rem;
}

div.productCard footer .priceBlock span{
	color: var(--primary-1, #10002B);
	font-family: var(--ff-body);
	font-size: var(--fs-15);
	font-style: normal;
	font-weight: var(--fw-bold);
	line-height: 133.2%; /* 1.998rem */
	letter-spacing: 0.14rem;
}

div.productCard footer .priceBlock span.original {
/*	color: #5C5862;*/
/*	color: #65616b;*/
	color: #6f6d73;
	font-weight: var(--fw-regular);
	text-decoration: line-through;
}

div.productCard footer .buttonsBlock {
	display: flex;
	align-items: center;
	gap: 1.6rem;
}

div.productCard footer .buttonsBlock a {
	position: relative;
	display: flex;
	padding: 0.8rem 1.55rem;
	align-items: center;
	gap: 0.8rem;
	align-self: stretch;
	border-radius: 1rem;
	font-size: var(--fs-12);
	font-weight: var(--fw-medium);
	line-height: 133.2%; /* 1.5984rem */
	letter-spacing: 0.1rem;
	text-transform: uppercase;
	transition: all .2s;
}

div.productCard footer .buttonsBlock a img {
	width: 2rem;
	height: 2rem;
}

div.productCard footer .buttonsBlock a.play {
	background: var(--white-40);
}

div.productCard footer .buttonsBlock a.play span {
	background: var(--gradient-default);
	background-clip: text;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

 div.productCard footer .buttonsBlock a:hover {
	background: white;
}
